import React, { useState,useContext,useEffect} from "react";
import { Box, Container, Button, Typography } from "@mui/material";
import {
    heroH1,
    heroBTN,
    arwimg,
} from "../components/style";
import useMediaQuery from '@mui/material/useMediaQuery';
import ghf1 from '../assets/1.gif'
import Chavron from "../components/Chavron";
import { Link } from "react-router-dom";
import { AppContext } from "../context/createContext";
import AOS from 'aos';
import 'aos/dist/aos.css';
import ReactPlayer from 'react-player'
import videogif from "../assets/video.mp4"

const HeroSection = () => {

    AOS.init();
    const { setData } = useContext(AppContext);
    const matches = useMediaQuery('(max-width:900px)');
    const match12 = useMediaQuery('(max-width:1200px)');
    const match40 = useMediaQuery('(max-width:520px)');



    
    return (
        <>
            <div  className={"scroldiv"}>
             <Box   data-aos="fade-up"
                    data-aos-offset="200"
                    data-aos-delay="50"
                    data-aos-duration="1000"
                    >
             <Container maxWidth="xl">
                    <Box className="mainhero">
                        <Box style={{ width: matches ? '100%' : "45%", textAlign: match40 ? 'center' : 'unset' }}>
                            <h1 className="animated-text">
                            Your Gateway To<br />  Financial Solutions
                            </h1>
                            <p className="hero_desc">
                            Abbass Finance specialises in a spectrum of loans, from home loans to commercial or business lending. Discovery tailored financial options that pave the way for your financial success.
                            </p>
                            <Link to='/Contact' onClick={() => setData("Contact Us")}>
                                <Button variant="contained" className="btncontect" style={heroBTN}>
                                    Contact Us
                                </Button>
                            </Link>
                        </Box>
                        <Box style={{ width: matches ? '100%' : "45%", display: 'flex', justifyContent: 'center' }}>
                            {/* <img src={ghf1} alt="" style={{ width: "80%", marginTop: '4rem' }} /> */}
                          <video src={videogif} autoPlay loop muted  className="video"/>
                        </Box>
                    </Box>
                </Container>
                <Box style={{ margin: `${match12 ? '5rem' : '-1rem'}  auto 0px auto`, width: 'fit-content' }}>
                    <Chavron />
                </Box>
                <br />
                <br />
                <Box style={{ ...arwimg, marginTop: '4rem' }}>
                    <h1 className="font_seth1">
                        Explore Our Services
                    </h1>
                </Box>
             </Box>
            </div>
        </>
    );
}

export default HeroSection;
