import React,{useRef,useEffect} from 'react'
import Typography from '@mui/material/Typography'
import { footerdiv, fotcontdiv, fotplcy } from './style'
import { Container, Box } from '@mui/material'
import useMediaQuery from '@mui/material/useMediaQuery';
import logo from '../assets/flogo.png'
import { Link ,useLocation } from 'react-router-dom';
import imgH from '../assets/map.png'


const Footer = () => {

  const matches = useMediaQuery('(max-width:500px)');
  const match70 = useMediaQuery('(max-width:700px)');
  const match12 = useMediaQuery('(max-width:1200px)');
  const location = useLocation()
  const cardsRef = useRef(null);

  useEffect(() => {
    if (!match12) {
      const observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              entry.target.style.transition = "opacity 2s, transform 2s";
              entry.target.style.opacity = 1;
              entry.target.style.transform = "translateY(0)";
              entry.target.style.transform = "overflow:auto;";
            } else {
              entry.target.style.transition = "opacity 2s, transform 2s";
              entry.target.style.opacity = 0;
              entry.target.style.transform = "translateY(20%)";
            }
          });
        },
        { threshold: 0.5 } 
      );
  
      if (cardsRef.current) {
        observer.observe(cardsRef.current);
      }
  
      return () => {
        if (cardsRef.current) {
          observer.unobserve(cardsRef.current);
        }
      };
    }
  }, [match12]);
  
  return (
    <>
      <div style={{ background: '#1C1C1C' }}  className={location.pathname === '/'?'scroldiv':''}>
        <div ref={ location.pathname === '/'? match12 ? null:cardsRef:null}>
          <Container maxWidth="xl" style={{ ...footerdiv, padding: matches ? '2rem 10px' : '2rem', }}>
            <Box style={{ ...fotcontdiv, textAlign: match70 ? 'center' : 'unset' }}>
              <Box style={{ ...fotplcy, flexDirection: match70 ? 'column' : 'row', textAlign: match70 ? 'center' : 'unset' }}>
                <Link to='/'>
                  <img src={logo} alt="" width={150} />
                </Link>
                <Typography variant="body1">
                  <img src={imgH} alt="" style={{marginBottom:"-3px"}} /> &nbsp;Servicing Australia wide
                </Typography>
                <Link to='TermsandConditions/' className='termlnk linktag'>
                  Terms and Conditions
                </Link>
                <Link to='/PrivacyPolicy' className='termlnk linktag'>
                  Privacy Policy
                </Link>
              </Box>
              <Typography variant="body1" style={{ width: '100%', textAlign: 'center', fontSize: '14px' ,fontFamily:"Gilroy"}}>
              Abbass Finance Pty Ltd (ABN 43 670 222 250 / Credit Representative Number 551870) has access to a panel of lenders through National Mortgage Brokers Pty Ltd., (ACN 093 874 376 / Australian Credit Licence 391209), which is a fully-owned subsidiary of Liberty Financial Pty Ltd (ACN 077 248 983 / Australian Credit Licence 286596). Acme Loans Pty Ltd has access to products including those from Liberty Financial.
              </Typography>
            </Box>
          </Container>
          <Typography variant="h6" className='copyrights'> 2024 Abbass Finance Pty Ltd. All rights reserved </Typography>
        </div>
      </div>
    </>
  )
}

export default Footer
