import React, { useContext, useEffect } from "react";
import { Container, Typography, Box } from "@mui/material";
import { polih6, plicydiv } from "../components/style";
import useMediaQuery from "@mui/material/useMediaQuery";
import { AppContext } from "../context/createContext";
import { useLocation } from "react-router-dom";


const PrivacyPolicy = () => {


  const uselocation = useLocation()
  const matches = useMediaQuery("(max-width:500px)");
  const { setData, setPaths } = useContext(AppContext)

  useEffect(() => {
    setData('')
    setPaths(uselocation.pathname)
  }, [uselocation])

  return (
    <div style={{ marginTop: "5rem" }}>
      <Container maxWidth="xl">
        <Box style={{ textAlign: "center", marginTop: "7rem" }}>
          <Typography
            variant="h6"
            fontSize={matches ? 30 : 40}
            fontWeight={600}
          >
            Privacy Policy
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Your privacy is important
          </Typography>
          <Typography variant="body1">
            Abbass Finance Pty Ltd, understands that the details you provide us with are not only private and personal but
            that how we collect, use, hold, exchange and protect your information is important to you. It's imperative that
            you feel secure whenever you deal with us. It's also important that you understand how we protect your privacy,
            as well as how, when and where we may use your personal information. This Privacy Policy is our commitment
            to you on what our policies towards your privacy are. You can be confident in the fact that we abide by and are
            bound by the Privacy Act and the Australian Privacy Principles <strong> (APPs)</strong> for the protection of personal information.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            What information do we collect?
          </Typography>
          <Typography variant="body1">
            We collect personal information. Personal information means any information or opinion about you as an
            identified individual, or an individual who is reasonably identifiable, whether the information or opinion is true or
            not, and whether the information or opinion is recorded in a material form or not. For example identity and
            contact details, marital status and financial information.
            We may also collect sensitive information for specific purposes. For example, information about your health to
            enable the credit provider to assess an application you make for mortgage protection insurance and calculate any
            premiums. If we need this type of information, we will ask for your consent.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            How do we collect your personal information?
          </Typography>
          <Typography variant="body1">
            We will always aim to collect your personal information directly from you. If for some good reason, we are
            unable to do so, we may involve another organisation. For example, we may need to request a credit report from
            a credit reporting body when you apply for a credit facility. Before doing so or prior to collecting the information
            we need, we will inform you of the type of organisations we intend to disclose your personal information to. We
            may also collect your personal information through third parties who undertake the collection on our behalf
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Why do we collect your personal information?
          </Typography>
          <Typography variant="body1">
            We collect and receive personal information directly from you or from other business referrers in order to
            conduct our business so that we can:
            <ul>
              <li>
                submit your application for credit to a supplier via our Aggregator partner National Mortgage Brokers
                Pty Ltd;
              </li>
              <li>identify and tell you via direct marketing communications about other products or services offered by us</li>
              <li>
                or a third party that may interest you (unless you tell us not to);
              </li>
              <li>administer our customer relationships;</li>
              <li>for internal purposes, including for reporting and analytical purposes;</li>
              <li>meet our legal and regulatory reporting obligations; </li>
              <li>establish a customer marketing program</li>
              <li>system development testing purposes;</li>
              <li>ensure your security when visiting our website and to learn which areas of the site are of most interest
                to you (see the section 'cookies' below); and</li>
              <li>any other purpose related to the above</li>
            </ul>
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Direct Marketing
          </Typography>
          <Typography variant="body1">
            As stated above, we may use the information we gather to provide you with targeted communications about
            products and services we think will be of interest to you. We may also send you details of offers or services
            provided by our business partners and related entities. These direct marketing offers and promotions are
            designed to make your life easier, not harder.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="body1">
            You may at any time choose not to receive these communications. Doing so will not cost you anything, and we
            commit to ensure you stop receiving any unwanted communications as soon as possible. If you do not want to
            receive such marketing communications, please write to us at the following address:
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="body1">
            The Privacy Officer at: <br />
            Abbass Finance Pty Ltd<br />
            470 St Kilda Road,<br />
            Melbourne VIC 3004
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Why do we exchange your information with another organisation?
          </Typography>
          <Typography variant="body1">
            As a Mortgage Broker, we aim to provide you with products and services which suit your requirements. That
            means we partner with other organisations who provide and support the products and services we offer. In order
            to submit your application to a supplier for consideration, we will need to provide such supplier with your
            personal information that we hold. As you will appreciate, in certain circumstances we may be compelled by law to disclose your personal
            information to various authorities. For instance, we may be required under the Anti-Money Laundering and
            Counter-Terrorism Financing Act to disclose details of financial transactions and we may also be compelled under
            tax laws to provide such information to regulators.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="body1">
            Other organisations which we may exchange your personal information with may include:
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="body1">
            <ul>
              <li>credit providers;</li>
              <li>credit reporting bodies;</li>
              <li>government and law enforcement agencies;</li>
              <li>our suppliers, associates, service providers or contractors (including, for example, call centre, stationery
                printing houses, mail houses, information technology, marketing agencies, and marketing research
                companies etc);</li>
              <li>any person who proposes to guarantee or has guaranteed repayment of any credit provided to you;</li>
              <li>mortgage insurers, brokers, originators and/or managers, financial consultants, accountants, lawyers or
                real estate agents;</li>
              <li>National Mortgage Brokers Pty. Ltd., our aggregator partner;</li>
              <li>those in connection with funding financial accommodation by means of an arrangement involving
                securitisation;</li>
              <li>third parties to enable them to contact you to offer their products and services or to determine if you
                would be interested in the products and services; and</li>
              <li>any person considering acquiring or taking an interest in our business.</li>

            </ul>
            This is to your benefit, and helps us to provide the products and services you would expect from Abbass Finance
            Pty Ltd.
            <br />
            <br />
            Before we disclose any of your personal information to any other organisation, we will take reasonable steps to
            ensure that:
            <br />
            <ul>
              <li>The organisation has the same commitment as us to the protection of your personal information; AND</li>
              <li>You have consented to us doing so. </li>
            </ul>
            By consent, we mean approval in writing, orally, or implied from your dealings with us
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Disclosure of personal information overseas
          </Typography>
          <Typography variant="body1">
            We may hold or process your information on servers located overseas for filtering, hosting or storage purposes,
            reporting and analytical purposes and for system development testing purposes. If we do this, we make sure
            that such organisations have the appropriate data handling and security arrangements in place to ensure
            compliance with this Privacy Policy. Australias Privacy Act may not apply to such overseas organisations.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Access to and correction of your personal information
          </Typography>
          <Typography variant="body1">
            It is important for us, and for you, that the information we hold is accurate and up to date. In this, we rely on
            you to help us. We ask that you contact us whenever there are any changes to your personal information, so
            that we can update our records. <br />
            The personal information we hold about you is yours to access and correct, and as such we can provide you with
            general information, such as your name, address and contact details quite easily. There is no fee for making the
            initial request. More detailed information may not be as easy for us to access, and if necessary, in some cases
            there may be an access charge to cover the time we spend locating, compiling and explaining the information
            you ask for. If there is an access charge, well give you an estimate up front and confirm that youd like us to
            proceed. Generally, the access charge is based on an hourly rate plus any photocopying costs or other out-ofpocket expenses. Youll need to make the payment before we start.
            <br />
            <br />
            The process of providing you with access to or correcting your personal information may take up to 30 working
            days from the date of your request. If we refuse to give you access to or correct your information, we must give
            you written notice setting out the reasons for the refusal and the mechanisms available for you to complain
            about the refusal.
            <br />
            <br />
            If you need to access or correct your detailed information, just write to us at the following address:
            <br />
            <br />
            The Privacy Officer at:<br />
            Abbass Finance Pty Ltd<br />
            470 St Kilda Road,<br />
            Melbourne VIC 3004
          </Typography>
        </Box>




        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Security and storage of your personal information<br />
            How long do we keep information about you?
          </Typography>
          <Typography variant="body1">
            By law, we are required to hold certain information about you, even after you are no longer a customer. We may
            also hold certain information about you if it is needed for a purpose which you are aware of or reasonably expect
            us to hold the information. When your information is no longer needed or required to be held by law, it will be
            destroyed or de-identified.
          </Typography>
        </Box>


        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            How safe and secure is the information we hold about you?
          </Typography>
          <Typography variant="body1">
            We will take great care to protect your personal information by storing it in a secure environment, and when the
            information is no longer needed for any purpose for which the information may be used or disclosed, it will be
            destroyed or permanently de-identified. We will also take reasonable steps to protect any personal information
            from misuse, loss and unauthorized access, modification or disclosure.
          </Typography>
        </Box>

        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            Online security
          </Typography>
          <Typography variant="body1">
            Our website uses encryption techniques to enhance your privacy and security when using Abbass Finances
            website.
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            What are 'cookies' and how do they work?
          </Typography>
          <Typography variant="body1">
            Cookies are small data files that are downloaded from our web servers, and stored on your hard drive. A cookie
            is a string of letters and numbers that uniquely identify the computer you are using, and the customer number
            and access code you may have used to register at the site. Cookies are used on the Abbass Finance website. We
            track a visitor's journey through our site. This allows us to see at a glance which pages and information is of
            most interest to visitors. This type of cookie contains no personal information at all; simply a record of your
            journey through the site. Most browsers can be configured to refuse to accept cookies. You can also delete
            cookies form your hard drive. However, doing so may hinder your access to valuable areas of information within
            our site.
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            If you have a privacy complaint
          </Typography>
          <Typography variant="body1">
            Abbass Finance values our customers. We will always aim to be fair and responsive. If you have a complaint,
            including a complaint about Abbass Finances compliance with the APPs, you have the right to expect that we will
            handle it in a friendly and professional way. When we receive a complaint, we look on it as valuable feedback
            that may help us to improve the services we offer and to ensure your needs are met in a satisfactory and
            appropriate manner. If you wish to complain, at any time, about the handling, use or disclosure of your personal
            information, or about Abbass Finance compliance with the APPs, just write to us at the following address:
            <br />
            <br />
            The Privacy Officer at: <br />
            Abbass Finance Pty Ltd <br />
            470 St Kilda Road, <br />
            Melbourne VIC 3004 <br /> <br />
            We will make all efforts possible to investigate your complaint, and advise you of the outcome as soon as
            possible. If the matter is not resolved to your satisfaction, you can then refer your complaint to the Australian
            Information Commissioner, who can be contacted at:
            <br /> <br />
            Office of the Australian Information Commissioner (OAIC)<br />
            Level 3<br />
            175 Pitt Street<br />
            Sydney NSW 2000<br />
          </Typography>
        </Box>
        <Box style={{ ...plicydiv, width: matches ? "100%" : "80%" }}>
          <Typography variant="h6" style={{ ...polih6 }}>
            More information
          </Typography>
          <Typography variant="body1">
            You can request further information about the way we manage the personal information that we hold by writing
            to:
            <br /> <br />
            The Privacy Officer at:<br />
Abbass Finance Pty Ltd<br />
470 St Kilda Road,<br />
Melbourne VIC 3004
          </Typography>
        </Box>
      </Container>
    </div>
  );
};

export default PrivacyPolicy;
